%_notification_message {
  margin: 1.3em auto;
  font-weight: bold;
  text-align: center;
  width: 400px;
  height: 64px;
  display: flex;
  align-items: center;
  justify-content: center;
}

%_checkmark_icon {
  background: #ccc url('data:image/svg+xml;base64,') no-repeat;
  background-size: 64px 64px;
}

%_cancel_icon {
  background: #ccc url('data:image/svg+xml;base64,') no-repeat 8px 8px;
  background-size: 48px 48px;
}

div.notification_wrapper {
  div.notification_correct{
    @extend %_notification_message;
    @extend %_checkmark_icon;
    color: #060;
  }

  div.notification_incorrect{
    @extend %_notification_message;
    @extend %_cancel_icon;
    color: #C00;
  }
}



